@import url("https://fonts.googleapis.com/css2?family=Alegreya + Sans + SC:wght@400;500;800");
* {
  box-sizing: border-box;
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.main {
  position: relative;
  min-height: 100%;
  min-width: 363px;
}

.section {
  display: flex;
}

.container {
  max-width: 1440px;
  margin: auto;
}

.wrap-block {
  display: block;
}

.wrap-inline {
  display: inline;
}

.row {
  display: flex;
}

.column {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
}

.popup {
  background-color: rgba(18, 18, 18, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
}

[data-visible=false] {
  display: none;
}

.title {
  color: #121212;
  font-size: 42px;
  font-weight: 700;
  /*text-align: center/**/
}

.btn {
  background: transparent;
  cursor: pointer;
  outline: 0px solid #5569fa;
  transition: all 0.45s ease-in-out;
}
.btn:hover {
  color: red;
  outline: 3px solid #5569fa;
}
.btn:focus {
  outline: 3px solid #5569fa;
  transition: all 0.15s ease-in-out;
}
.btn--brd0 {
  border: 0px solid #000;
  border-radius: 3px;
}

.alert {
  position: relative;
  background: #fff;
  width: 100%;
  /*height: 100%/**/
  max-width: 920px;
  max-height: 210px;
  min-width: 363px;
  /*min-height: 210px/**/
  margin: auto;
  padding-left: 15px;
}
.alert__btn-close {
  position: absolute;
  top: 26px;
  right: 26px;
}
.alert__wrap-btn-reset {
  padding-left: 1rem;
}
